<template>
  <div class="notes container">
    <h1>Notas</h1>
    <b-alert
            :show="dismissCountDown"
            dismissible
            :variant="message.color"
            @dismissed="dismissCountDown=0"
            @dismiss-count-down="countDownChanged"
    >
      <p>{{ message.title }}</p>
    </b-alert>
    <form @submit.prevent="addNote" v-if="!edit">
      <h3>Agregar nueva nota</h3>
      <label for="name"/>
      <input v-model="note.name" id="name" type="text" class="form-control" placeholder="Nombre">
      <label for="description"/>
      <input v-model="note.description" id="description" type="text" class="form-control" placeholder="Descripción">
      <b-button type="submit" class="btn btn-success btn-block">Agregar</b-button>
    </form>
    <form @submit.prevent="editNote(note)" v-if="edit">
      <h3>Editar nota</h3>
      <label for="nameEdit"/>
      <input v-model="note.name" id="nameEdit" type="text" class="form-control" placeholder="Nombre">
      <label for="descriptionEdit"/>
      <input v-model="note.description" id="descriptionEdit" type="text" class="form-control" placeholder="Descripción">
      <b-button type="submit" class="btn btn-warning btn-block">Editar</b-button>
      <b-button type="submit" class="btn btn-info btn-block" @click="cancelEdit">Cancelar</b-button>
    </form>
    <table class="table table-striped">
      <thead>
      <tr>
        <th scope="col">#</th>
        <th scope="col">Nombre</th>
        <th scope="col">Descripción</th>
        <th scope="col">Acciones</th>
      </tr>
      </thead>
      <tbody>
      <tr v-for="note in notes" :key="note.id">
        <th scope="row">{{ note._id }}</th>
        <td>{{ note.name }}</td>
        <td>{{ note.description}}</td>
        <td>
          <b-button class="btn btn-warning btn-sm" @click="modifyNote(note._id)">
            Editar
          </b-button>
          <b-button class="btn btn-danger btn-sm" @click="deleteNote(note._id)">
            Eliminar
          </b-button>
        </td>
      </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
  import {mapState} from 'vuex'
  export default {
    data() {
      return {
        notes: [],
        dismissSecs: 10,
        dismissCountDown: 0,
        showDismissibleAlert: false,
        message: {
          color: '',
          title: ''
        },
        note: {
          name: '',
          description: ''
        },
        edit: false
      }
    },
    computed: {
      ...mapState(['token'])
    },
    created() {
      this.getNotes();
    },
    methods: {
      getNotes() {
        let config = {
          headers: {
            token: this.token
          }
        };
        this.axios.get('/notes', config)
          .then(notes => {
            this.notes = notes.data
          })
          .catch(error => {
            console.log(error.response)
          })
      },
      getNote(id) {
        this.axios.get(`/note/${id}`)
          .then(note => {
            this.note = note.data
          })
          .catch(error => {
            console.log(error.response)
          })
      },
      addNote() {
        let config = {
          headers: {
            token: this.token
          }
        };
        this.axios.post('/note/new', this.note, config)
          .then(note => {
            this.notes.push(note.data)
            this.note = {}
            this.showAlert('success', 'Nota agregada correctamente')
          })
          .catch(error => {
            if (error.response.data.error.errors.name.message) {
              this.showAlert('danger', error.response.data.error.errors.name.message)
            }
          })
      },
      deleteNote(id) {
        this.axios.delete(`/note/${id}`)
          .then(response => {
            const index = this.notes.findIndex(note => note._id === response.id);
            this.notes.splice(index, 1);
            this.showAlert('success', 'Nota eliminada correctamente')
          })
          .catch(error => {
            if (error.response.data.error.errors.message) {
              this.showAlert('danger', error.response.data.error.errors.message)
            }
          })
      },
      modifyNote(id) {
        this.edit = true;
        this.getNote(id);
      },
      editNote(note) {
        this.axios.put(`/note/${note._id}`, note)
          .then(response => {
            const index = this.notes.findIndex(eachNote => eachNote._id === response.data._id);
            this.notes[index].name = response.data.name;
            this.notes[index].description = response.data.description;
            this.showAlert('success', 'Nota editada correctamente')
            this.edit = false;
            this.note = {}
          })
          .catch(error => {
            if (error.response.data.error.errors.message) {
              this.showAlert('danger', error.response.data.error.errors.message)
            }
          })
      },
      cancelEdit() {
        this.edit = false;
        this.note = {}
      },
      countDownChanged(dismissCountDown) {
        this.dismissCountDown = dismissCountDown
      },
      showAlert(color, title) {
        this.dismissCountDown = this.dismissSecs
        this.message.color = color
        this.message.title = title
      }
    }
  }
</script>
